import pop from '@/components/promptOptions/pop.vue'
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	components:{
		uniLoadMore,
		pop
	},
	data(){
		return{
			uid:'',
			token:'',
			active:0,
			orderList:[
				{name:'asdasdasds'},{name:'asdasdasds'},{name:'asdasdasds'},{name:'asdasdasds'}
			],
			sh:'',
			dataList:[],
			page:1,
			pageSize:5,
			total:0,
			more:'',
			orderno:'',
			keywords:''
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   onLoad(){
		  
		   
	   },
	methods:{
		init(){
			var that=this
			var userInfo=uni.getStorageSync('userInfo'),that=this
			if(userInfo!='' & userInfo!=null & userInfo!=undefined){
				this.uid=userInfo.uid
				this.token=userInfo.token
			}
			this.getList(this.page,this.orderno,this.keywords)
			 setTimeout(function(){
			 	that.$getHeight('#sv',(res) =>{
			 		that.sh=res
			 	})
			 },0)
		},
		choosed(m){
								 uni.$off('popChoosed')
		},
		open(){
								 var that=this
								 uni.$on('popChoosed',function(e){
									 that.choosed(e)
								 })
								 this.$refs.order.show=true
		},
		toDetail(no){
			
			uni.navigateTo({
				url:'./operating/orderDetail?orderno='+no
			})
		},
		toggle(t){
			this.active=t
			this.reset()
			this.getList(this.page,this.orderno,this.keywords)
		},
		reset(){
			this.page=1
			this.total=0
			this.orderno=''
			this.keywords=''
			this.dataList=[]
			this.more=''
		},
		getList(p,no,k){
			var that=this
			var params={
				status:this.active,
			  page:p,
			  pagesize: this.pageSize,
			  uid:this.uid,
			  token:this.token,
			  orderno:no,
			  keywords:k
			}
			if(this.page==1){
				this.$loading()
			}
			  var url='&r=api.myshop.orders'
			  this.$apiPost(url,params).then((res) =>{
				  that.total=res.total
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page,this.orderno,this.keywords)
		  // },2000)
		},
		search(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
			this.getList(this.page,this.orderno,this.keywords)
		},
		confirmDoSend(no){
			this.$loading()
			var that=this
			var params={
			  uid:this.uid,
			  token:this.token,
			  orderno:no,
			}
			  var url='&r=api.myshop.orders.doSend'
			  this.$apiPost(url,params).then((res) =>{
				  that.$msg('发货成功')
				  	uni.hideLoading()
					that.reset()
					that.getList(that.page,that.orderno,that.keywords)
			  })
		}
	}
}
